html,body{height:100%;width:100%}#root{position:relative;background-color:#1d1d1d;color:#fff;flex-direction:row;overflow-y:hidden;overflow-x:hidden;height:100%;width:100%}#title{width:fit-content}#title h2{overflow:hidden;border-right:.15em solid #22af22;margin-top:30px;white-space:nowrap;font-size:2.5rem;font-weight:900;color:#22af22;letter-spacing:-0.125rem;animation:typing 1s steps(20, end),blink-caret .75s step-end infinite}p{overflow:hidden;font-size:1rem;font-weight:600;color:#22af22}div{display:flex}ol{column-count:2}li{position:relative;list-style-type:"$";color:#22af22;margin-bottom:25px;left:-10px}li span{position:relative;left:10px}img{max-height:100%;max-width:100%}#welcomeScreen{position:absolute;justify-content:center;align-content:center;height:100%;width:100%}#textBox{flex-direction:column;width:100%;margin-top:auto;margin-bottom:auto;align-items:center;text-align:center}#welcomeText{overflow:hidden;border-right:.15em solid #22af22;white-space:nowrap;font-size:4rem;font-weight:900;color:#22af22;letter-spacing:-0.125rem;animation:typing 3.5s steps(40, end),blink-caret .75s step-end infinite}#options{width:50%;flex-direction:row;justify-content:space-evenly}#skillsBtn,#aboutBtn,#contactBtn,#projectsBtn{border:none;background:none;color:#22af22;font-family:"Roboto Mono",monospace;font-weight:300;font-size:1rem}#skillsBtn:hover,#aboutBtn:hover,#contactBtn:hover,#projectsBtn:hover{color:#187818}#popUp{visibility:hidden;z-index:-1;scale:0;width:calc(100% - 6px);height:calc(100% - 6px);flex-direction:column;background-color:#1d1d1d;margin:auto;box-shadow:0px 0px 1px 3px gray;transition:1s}#popUp.show{visibility:visible;z-index:1;scale:1}#popUp p{margin:auto}#topBar{align-self:flex-start;justify-content:end;width:100%;height:20px;background-image:linear-gradient(to right, rgb(0, 0, 195), rgb(116, 137, 255));flex-direction:row}#closeLink{display:flex;align-content:center}#closeButton{align-self:center;height:14px;width:14px;margin:3px;padding:0px;background-color:#b9b9b9;border-style:none;box-shadow:inset -0.5px -0.5px 1px 0px #000,inset .5px .5px 1px 0px #fff;position:relative}#closeButton:after{position:absolute;bottom:1px;left:0;right:0;content:"Ã—";font-size:12px;font-weight:bold;color:#000;text-align:center}#screen{width:100%;justify-content:center}#aboutScreen{width:50%;flex-direction:column;align-items:center;margin-top:auto;margin-bottom:auto}#bottom{flex-direction:row}#description{width:50%;flex-direction:column;padding-right:30px}#picture{width:50%;justify-content:center;align-content:center;background-color:#1d1d1d}#profilePicture{filter:blur(10px) grayscale(100%) opacity(1);transition:.4s}#profilePicture:hover{filter:none;transition:.4s}#list{display:flex;flex-direction:column;margin-top:25px}#mem{height:100%;width:100%;justify-content:center;align-items:center}#mem img{height:85%;width:85%;object-fit:contain}#skillsScreen{flex-direction:column;justify-content:center;align-items:center;width:60%;margin:0px auto auto auto}#skills{flex-direction:row;justify-content:center;width:100%}#firstColumn,#secondColumn{display:flex;flex-direction:column;justify-content:center;align-content:space-between;margin-left:10%;margin-right:10%;width:50%}#skill{flex-direction:column;margin-top:30px;margin-bottom:20px;position:relative;justify-content:center;align-items:center}#skill h2,h3{margin-top:10px;margin-bottom:10px;color:#22af22}#skillBar{width:300px;height:25px;box-shadow:0px 0px 0px 5px #22af22;align-items:center;flex-direction:row;justify-content:flex-start;padding:2.5px}#skillBar span{width:calc(5% - 2.5px);height:100%;margin-right:2.5px;background-color:#22af22}#contactScreen{flex-direction:column;justify-content:center;align-content:space-around;margin:0px auto 0px auto}#email,#linkedin{flex-direction:column;justify-content:center;align-content:center;align-items:center;margin:70px 0 10px 0}#email h2,#linkedin h2{font-size:2rem;color:#22af22;margin:10px}#emailLink,#linkedin a{text-decoration:none;color:#22af22;font-family:"Roboto Mono",monospace;font-weight:300;font-size:1.2rem}#emailLink:hover,#linkedin a:hover{color:#187818}#underConstruction{justify-content:center;align-items:center;height:100%;width:100%}#underConstruction h1{overflow:hidden;border-right:.15em solid #22af22;white-space:nowrap;font-size:4rem;font-weight:900;color:#22af22;letter-spacing:-0.125rem;animation:typing 3.5s steps(40, end),blink-caret .75s step-end infinite}@keyframes typing{from{width:0}to{width:100%}}@keyframes blink-caret{from,to{border-color:rgba(0,0,0,0)}50%{border-color:#22af22}}